import React, {useEffect, useState, useRef} from 'react';
import InfoExpanded from './infoExpanded'
import Preguntas from './preguntas'
import PatternSvg from './patternSvg'

const About2 = () => {

    var titulo = "Nuestras preguntas de investigacion...";
    var sub = "Dedicamos un proceso de investigacin transdisciplinaria a la comprensin del problema. Sobre la dimensin adquirida, imaginamos y a veces realizamos estrategias tecno-culturales para descubrir nuevas soluciones."

    var pregunta1 = "Cmo potenciar y expandir la transferencia de conocimientos a la poblacin?";
    var pregunta2 = "Qu futuros imaginamos??";
    var pregunta3 = "Cmo imaginar otros modelos polticos, sociales, productivos?";
    var pregunta4 = "Cmo trascender la obra, el museo, el sistema del arte?";
    var pregunta5 = "Por qu es importante el arte para el futuro de la humanidad?";
    var pregunta6 = "Existen otras topologas que doten a las disciplinas artsticas de nuevas posibilidades de accin y produccin?";

    var modalT = "Mtodologa";
    var modalP = "Nuestro enfoque crtico sobre los avances y las limitaciones de la tecnologa funcional nos llev a plantear la necesidad de producir dispositivos/interfaces que estimulen, por medio del lenguaje simblico del arte electrnico, aspectos creativos, sensoriales, adaptativos y exploratorios de quienes son parte de las experiencias que llevamos adelante.  Trabajamos con las comunidades que viven diferentes problemticas o con problemas especficos que identificamos o que plantea una institucin, colega, cliente. ";
    return (
        <div className="about2">
            <div className="b_boxes b2">
                <Preguntas dur={"40s"} fSize={50} cCaption={pregunta1} bgColor={""} txColor={"#ADADAD"}/>
                <Preguntas dur={"20s"} fSize={80} cCaption={pregunta2} bgColor={""} txColor={"#3B3B3B"}/>
                <Preguntas dur={"30s"} fSize={60} cCaption={pregunta3} bgColor={""} txColor={"#EDEDED"}/>
                <Preguntas dur={"20s"} fSize={40} cCaption={pregunta4} bgColor={""} txColor={"#FAFAFA"}/>
                <Preguntas dur={"15s"} fSize={30} cCaption={pregunta5} bgColor={""} txColor={"#D4D4D4"}/>
                <Preguntas dur={"30s"} fSize={40} cCaption={pregunta6} bgColor={""} txColor={"#ADADAD"}/>
               
            </div>
            <div className="b_boxes b3"></div>
            <div className="b_boxes b4"></div>
            <div className="b_boxes b5">
                <InfoExpanded titulo={titulo} descripcion={sub} bgColor="#eeeeee" modalT={modalT} modalP={modalP}  />
            </div>
            <div className="b_boxes b6"></div>
            
        </div>
    );
}

export default About2;
