import React, {useState, useEffect} from 'react';
import InfoExpandedx2 from './infoExpandedx2'
import ItIso from './it_iso'

const About3 = () => {

    var titulo = "Nuestro Aporte"
    var descripcion ="La aspiracin mxima de los proyectos que se realizan en Intercambios Transorgnicos no se trata del arte por el arte en s mismo, sino de la circulacin de obras, ideas, productos, procesos y el diseo de estrategias que buscan encontrar suelo en problemticas sociales especficas" ;
    var descripcion2 ="Todo esto lo hacemos vinculando al MUNTreF con instituciones interesadas en integrar diversos medios y tecnologas a sus estrategias de trabajo."
    var modalT = "Porque queremos que el dilogo entre arte y ciencia sea realmente tangible.";
    var modalP = "Activamos conversaciones transdisciplinarias y exploraciones de campo que nos ofrezcan la matriz de datos que sustenta nuestra investigacin. As es como guiamos, reformulamos y expandimos continuamente nuestra perspectiva crtica sobre el uso validado de la tecnologa en salud y educacin.";
    var modalP2 = "Representamos a nuestra universidad y adems consolidamos nuevos lazos con pares y organizaciones a travs de conferencias, encuentros, exposiciones y workshops que nos llevaron a recorrer diversas ciudades e instituciones de Amrica Latina y Europa.  "
    var modalP3 = "Nuestra misin es tan ambiciosa como inquietante. Nos interesa generar mayor empoderamiento de las personas con una discapacidad, colaborar en comprender la dimensin cultural de la salud. Aportar a las transformaciones sociales contemporneas en torno a la valoracin y conceptos de salud, alimentacin, independencia personal, comunidad, soberana tecnolgica, soberana sanitaria. ";
    var[icohovered, seticoHovered] = useState(false);
    function handleHover(){
        seticoHovered(true);
    }

    function handleLeave(){
        seticoHovered(false);
    }
    
    return (
        <div className="about3">
            <div className="c_boxes c1"></div>
            <div className="c_boxes c2"  onMouseEnter={handleHover} onMouseLeave={handleLeave}>
                <ItIso action={icohovered} />
            </div>
            <div className="c_boxes c3">
                {/* <div className="about3_content">
                    <h1>Nuestro Aporte:</h1>
                </div> */}
            </div>
            <div className="c_boxes c4"></div>
            <div className="c_boxes c5">
            <InfoExpandedx2 titulo={titulo} desc={descripcion} desc2={descripcion2} bgColor="#7c428c" txColor={"#eeeeee"} modalT={modalT} modalP={modalP} modalP2={modalP2} modalP3={modalP3}/>
            </div>
            <div className="c_boxes c6">
                
            </div>
            
        </div>
    );
}

export default About3;
